body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toolbar {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #777;
    height: 46px;
}

.toolbar__navigation {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}

.toolbar__logo a {
    color: white;
    text-decoration: none;
    font-size: 2rem;
}

.toolbar__navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar__navigation-items li {
    font-size: 1.6rem;
    padding: 0 .5rem;
}

.toolbar__navigation-items a {
    color: white;
    text-decoration: none;

}

.toolbar__navigation-items a:hover,
.toolbar__navigation-items a:active {
    color: #7bff2f;
}

.spacer {
    flex: 1 1;
}

.logo-img {
    padding: 0 1rem;
}
.main-heading {
    color: orangered;
}


.grid-container {
    font-family: Roboto, sans-serif;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 1.5rem;
    border: 1px solid black;
    background: #C4C4C4;
    border-radius: 5px;
    display: grid;
    padding: .15rem;
    grid-row-gap: .1rem;
    grid-column-gap: .1rem;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . ." ". . . . . . . . . . . . .";
  }

  .cell {
      border: 1px solid black;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      height: 3rem;
      width: 3rem;
      align-items: center;
      background: #F2F2F2;
  }

  .cell:hover {
      cursor: pointer;
      border: 1px solid #cefdb3;
      background: #cefdb3;
}
* {
    margin: 0;
    padding: 0; }

  *,
  *::before,
  *::after {
    box-sizing: inherit; }

html {
    font-size: 62.5%;
    background: #4B4B4B;
}

.main {
    margin-top: 54px;
    padding: 0 1rem;
}
